<template>
  <div class="list nes-container with-title">
    <p class="title">My skills</p>

    <p v-if="isLoading">Loading...</p>
    <p v-if="fetchError" class="nes-text is-error">The database is unreachable...</p>

    <skill-card
      v-for="skill in skills"
      :key="skill.name"
      v-bind:name="skill.name"
      v-bind:mark="skill.mark"
    ></skill-card>
  </div>
</template>

<script>
import SkillCard from "./SkillCard.vue";

export default {
  name: "SkillsList",
  components: {
    SkillCard,
  },
  data: function () {
    return {
      skills: [],
      isLoading: true,
      fetchError: false
    };
  },
  methods: {
    getSkills: async function () {
      if(window.fetch){
        try{
          const res = await fetch('https://valdorah-skills.herokuapp.com/skills', {method: 'GET', mode: 'cors'});
          const skills = await res.json();
          this.fillSkillsList(skills);
          this.isLoading = false;
        }
        catch(err){
          this.fetchError = true;
        }
      }
      else{
        const currentVueObject = this;
        const request = new XMLHttpRequest();
        request.onload = function(){
          const skills = JSON.parse(this.responseText);
          currentVueObject.fillSkillsList(skills);
          currentVueObject.isLoading = false;
        }
        request.onerror = function(){
          currentVueObject.fetchError = true;
        }
        request.open('get', 'https://valdorah-skills.herokuapp.com/skills', true);
        request.send();
      }
    },
    fillSkillsList: function(skills){
      skills.forEach(skill => {
        this.skills.push(skill);
      });
    }
  },
  mounted: function () {
    this.getSkills();
  },
};
</script>

<style scoped>
.list{
  margin-top: 30px;
}
</style>