<template>
  <div class="skill">
    <label>{{ name }}</label>
    <progress class="nes-progress is-success" max="100" value="0"/>
  </div>
</template>

<script>
export default {
  name: "SkillCard",
  props: {
    name: String,
    mark: Number,
  },
  data: function () {
    return {
      i: 0,
    };
  },
  methods: {
    move: function (elt, stop) {
      if (this.i == 0) {
        this.i = 1;
        let width = 1;
        let id = setInterval(function () {
          if (width >= stop * 10) {
            clearInterval(id);
            this.i = 0;
          } else {
            width++;
            elt.value = width;
          }
        }, 10);
      }
    },
    fillLoaderInside: function () {
        const progress = this.$el.lastChild
        this.move(progress, this.mark)
    },
  },
  mounted: function () {
    this.fillLoaderInside();
  },
};
</script>

<style scoped>
.skill {
  margin: 40px 0;
}

.skill-name{
  text-align: center;
  margin: 5px;
}

.loader {
  width: 100%;
  height: 40px;
}

.loader-inside {
  background-color: #afb42b;
  width: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: end;
}
</style>